<template>
<div id="audios">
  <el-container>
    <el-header  height="30px">
            <div class="command">
              <div>{{currentData}}</div>
          <div class="btns">
            <span class="add"><AddPop @changeResult="getPage($event)"></AddPop></span>
            <span class="upload"><OneLoad @changePageData="getPage($event)"></OneLoad></span>
            <span class="dowmload"><DownLoad></DownLoad></span>
            <span class="delete"><ComDelete></ComDelete></span>
          </div>
            </div>
    </el-header>
     <el-container>
        <el-aside width="180px">
          <el-col :span="24">
            <div class="right">
              <ResultList></ResultList>
            </div>
          </el-col>
        </el-aside>
        <el-container>
    <el-main  >
      <!-- <div class="pic"> -->
      <el-row >
          <el-col class="empty" v-show="audioData.total_files == 0">
            <div>
              <img src="~assets/images/kong.png" alt="" />
              <div>暂无数据</div>
            </div>
          </el-col>
        <el-col 
            class="noempty"
            :span="6"
            v-for="(item, i) in audioData.audioOptions"
            :key="item + i"
            v-show="audioData.total_files != 0"
          >
            <div class="col-content">
              <audio ref='audio'
            :src="item"
           type=audio/mpeg  controls ></audio>
            </div>
          </el-col>
          <!-- <div class="col-content">
            <audio 
            src="http://www.170mv.com/kw/antiserver.kuwo.cn/anti.s?rid=MUSIC_72217588&response=res&format=mp3|aac&type=convert_url&br=128kmp3&agent=iPhone&callback=getlink&jpcallback=getlink.mp3"
            width=300 height=45 type=audio/mpeg  controls volume="0"></audio>
          </div> -->
      
      </el-row>
      <!-- </div> -->
    </el-main>
    <el-footer>  
        <div class="footer" height="30px">
            <el-pagination
              @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            :layout="layout"
              :total="audioData.total_files">
            </el-pagination>   
      </div>
    </el-footer>
       </el-container>
    </el-container>
  </el-container>
</div>
</template>
<script>
import AddPop from "../DetailsComponent/AddPop.vue";
// import UpLoad from "../DetailsComponent/UpLoad.vue";
import OneLoad from "../DetailsComponent/OneLoad.vue";
import DownLoad from "../DetailsComponent/DownLoad.vue";
import ComDelete from "../DetailsComponent/ComDelete.vue";
import ResultList from "../DetailsComponent/ResultList";
export default {
    components: {
    AddPop,
    OneLoad,
    DownLoad,
    ComDelete,
    ResultList,
  },
  data() {
    return {
       currentPage: 1,
      //总条数
      // total: null,
      //当前每页条数
      pageSize: 9,
      //控制每页有多少条数
      pageSizes: [9, 12],
      //控制 分页组件的布局顺序
      layout: "total, sizes, prev, pager, next, jumper",
      imagsList:[
        {
          id:0,
          details:'已标注',
          url:"https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
        },
        {
          id:1,
          details:'未标注',
          url:"https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
        },
        {
          id:2,
          details:'已标注',
          url:"https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
        },
        {
          id:3,
          details:'已标注',
          url:"https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
        },
        {
          id:4,
          details:'未标注',
          url:"https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
        },
      ],
      audioData: {
        audioOptions: [],
        total_files: 0, //视频总条数
        total_pages: "",
      },
      currentData:"",
      loading:""
    };
  },
 computed: {
    // 选中一级类型
    currentType() {
      return this.$store.state.ItemDetails.currentType;
    },
    // 二级导航栏的选中值
    itemDate() {
      return this.$store.state.ItemDetails.itemDate;
    },
    // 二级导航栏信息
    menulist() {
      return this.$store.state.ItemDetails.menulist;
    },
  },
  watch: {
    // 监听二级导航栏的选中值
    itemDate: {
      handler(newValue, oldValue) {
        if (this.currentType == "audio") {
          //  console.log(newValue, oldValue);
        this.currentData = newValue;
        // console.log(this.currentData);
        // this.getPageData();
        this.audioData.audioOptions =[]
        this.getPage();
      }
        }
       
    },
  },
  methods:{
    getPage() {
      
      if(this.$store.state.ItemDetails.currentType ==="audio"){
this.$api
        .getPageData({
          dataSetName: this.currentData,
           page_number: this.currentPage,
          page_size: this.pageSize,
        })
        .then((res) => {
          this.loading = this.$loading({
        lock: true,
        text: "正在加载图片...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
          console.log(res);
          if (res.code === 200) {
            this.audioData.audioOptions = res.fileList;
            this.audioData.total_files = res.total_files;
            this.audioData.total_pages = res.total_pages;
            // this.total = res.total_files;
          }
          this.loading.close();
        });
      }
      
    },
    handleSizeChange(size) {
      console.log(`每页 ${size} 条`);
      this.pageSize = size;
      this.getPage();
    },
    handleCurrentChange(page) {
      console.log(`当前页: ${page}`);
      this.currentPage = page;
      this.getPage();
    },
  },
  mounted() {
    // this.reLoad();
  },
}
</script>
<style lang="scss" scope >
#audios{
    min-width: 1200px;
 .el-container{
   .el-header{
     padding: 0;
     .command{
       display: flex;
         color: #fff;
       justify-content: space-between;
       align-items: center;
       height: 30px;
       padding: 0 2vh;
       background-color: #222337;
     }
      .btns {
          display: flex;
          height: 30px;
          // background-color: #f5f4f8;
          justify-content: right;
          padding-right: 10px;
          span {
            margin-right: 5px;
          }
        }
   }
     .el-aside {
      // min-height: 860px;
      // max-height: 860px;
      overflow: hidden;
      height: calc(100vh - 132px);
    }
   .el-main{
    padding: 0;
      height: calc(100vh - 172px);
      // min-height: 745px;
      // max-height: 745px;
      // overflow: auto;
         .el-col {
        // margin-right: 0.2rem;
        padding-left: 1rem;
      }
      .empty {
        // display: none;
        display: flex;
        // height: calc(100vh - 172px);
       height: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        box-sizing: border-box;
        padding: 40px 0;
        background: #d3dce6;
      }
      .noempty {
        .col-content {
          display: flex;
          position: relative;
          width: 100%;
          height: 12rem;
          justify-content: center;
          align-items: center;
          background-color: seashell;
          border-radius: 4px;
          margin: 4px 0;
          min-height: 36px;
          background: #fff;
          &:nth-child(2n) {
            // margin-right: 0;
            padding-right: 0;
          }
        }
        video {
          height: 100%;
          width: 70%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
        }
        .el-table {
          position: absolute;
          height: 100%;
          width: 30%;
          top: 0;
          left: 70%;
          bottom: 0;
        }
      }

   }
   .el-footer{
    padding: 0;
      margin-bottom: 10px;
     .footer{
       display: flex;
       justify-content: center;
       align-items: center;
      //  height: 30px;
       background-color: #222337;

     }
   }
 }
}


 
  
</style>

